@import '../../Theme/constants';

.icon {
  height: 36 * $unit;
  width: 36 * $unit;
  margin-inline-end: 14 * $unit;
  float: left;
}

[dir='rtl'] .icon {
  transform: none;
}

.uploadList {
  display: inline-block;
  padding: 0;
  margin-top: 48 * $unit;
  margin-bottom: 56 * $unit;
}

.uploadListItem {
  display: flex;

  margin-bottom: 24 * $unit;
  list-style: none;
  text-align: left;
}

.documentUploadedLabel,
.selfieUploadedLabel,
.videoUploadedLabel,
.activeVideoUploadedLabel {
  /* placeholder to namespace this with CSS modules */
}

.listText {
  line-height: 2.25;
}
